<template>
  <div class="container">
    <h1>Latest Posts</h1>
    <!--- Create Post Here -->
    <hr>

    <div>
      <label for="create-post">Say Something...</label>
      <input type="text" id="create-post" v-model="text" placeholder="Create a post">
      <button v-on:click="createPost">Post!</button>
    </div>
    <!---
    <p class="error" v-if="error">{{error}}</p>
    -->
    
    <div class="posts">
      <div class="post" v-for="(post) in posts">
        <p>{{`${post.createdAt.getFullYear()}/${post.createdAt.getMonth()}/${post.createdAt.getDate()}`}}</p>
        <p>{{post.text}}</p> 
        <button v-on:click="deletePost(post._id)">DeletePost</button>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from '@/PostServer';

export default {
  name: 'Post',
  data() {
    return{
      posts: [],
      error: [],
      text: ''
    }
  },
  async created() {
    try{
      this.posts = await PostService.getPost();
    } catch(err) {
      this.error = err.message;
    }
  },
  methods: {
    async createPost() {
      try{
        await PostService.insertPost(this.text);
        this.posts = await PostService.getPost();
      }
      catch (err){
        this.error = err.message;
      }
    },

    async deletePost(id) {
      try{
        await PostService.deletePost(id);
        this.posts = await PostService.getPost();
      }
      catch(err){
        this.error = err.message;
      }
    }
  }
}
</script>

